@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,900;1,100;1,300;1,900&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.6.1/font/bootstrap-icons.css);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

root {
	--jet: #353535ff;
	--ming: #3c6e71ff;
	--white: #ffffffff;
	--gainsboro: #d9d9d9ff;
	--indigo-dye: #284b63ff;
}

.icons img {
	margin: 1rem;
	width: 10rem;
	height: 3rem;
}

#contact-form {
	height: 80vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	align-content: center;
}

input {
	width: 70vw;
}

textarea {
	width: 70vw;
}

.card {
	width: 20rem;
}

#dz-border {
	border: 0.25rem solid #d9d9d9ff;
}

.card-body {
	background-color: #353535ff;
}

#title {
	text-align: center;
	color: #353535ff;
}

.portfolio {
	padding: 2rem;
	display: flex;
	justify-content: center;
}

.main {
	color: #d9d9d9ff;
	background-color: #284b63ff;
	border: 1rem solid #3c6e71ff;
	font-family: "Montserrat";
	height: auto;
}

.header {
	background-image: url(/dz-react-portfolio/static/media/pexels-matt-hardy-3560168.717046e6.jpg);
	background-size: cover;
	border: 1rem solid #284b63ff;
	font-family: "Montserrat";
}

/* header {
	display: flex;
	align-items: center;
	justify-content: space-between;
} */

h1 {
	color: #d9d9d9ff;
	background-color: #3c6e71ff;
	padding: 1rem;
	text-align: center;
	/* justify-self: start; */
}

.navbar {
	background-color: #3c6e71ff;
}

.footer {
	border: 1rem solid #284b63ff;
	position: relative;
	bottom: 0;
	width: 100%;
	background-color: #3c6e71ff;
}

ul {
	display: flex;
	justify-content: center;
}

li {
	list-style: none;
	margin: 0.5rem 2rem;
}

i {
	font-size: 3rem;
	color: #d9d9d9ff;
}

i:hover {
	color: #353535ff;
}

